body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 18px;
  font-family: "Arial", sans-serif;
  color: #444444;
  background: #ffffff;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.mobile-container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
.tablet-container {
  width: 708px;
  padding: 0 30px;
}
.desktop-container {
  width: 1220px;
  padding: 0 40px;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background: #68b738;
}
.button:hover {
  background: #5eaa2f;
}
.button:active {
  background: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--gray {
  background: #f2f2f2;
  color: #444444;
}
.button--gray:hover {
  background: #ebebeb;
}
.button--gray:active {
  background: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .button {
    padding-top: 17px;
    padding-bottom: 15px;
    font-size: 20px;
    line-height: 20px;
  }
}
.page-header {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
.page-header__container {
  width: 320px;
  margin: 0 auto;
}
.page-header__top {
  display: flex;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  padding-right: 20px;
  border-bottom: 1px solid #e6e6e6;
}
.page-header__logo img {
  width: 191px;
  height: 38px;
  cursor: pointer;
}
.page-header__toggle-menu {
  position: relative;
  width: 24px;
  height: 18px;
  margin-left: auto;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.page-header__toggle-menu--opened::before,
.page-header__toggle-menu--opened::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.page-header__toggle-menu--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle-menu--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle-menu--nojs {
  display: none;
}
@media (min-width: 768px) {
  .page-header {
    margin-bottom: 0;
  }
  .page-header__container {
    display: flex;
    justify-content: space-between;
    width: 708px;
    padding: 0 30px;
    padding-top: 25px;
  }
  .page-header__toggle-menu {
    display: none;
  }
  .page-header__top {
    padding: 0;
    border: none;
  }
  .page-header__logo img {
    width: 173px;
    height: 58px;
  }
  .page-header__logo img:hover {
    opacity: 0.8;
  }
  .page-header__logo img:active {
    opacity: 0.6;
  }
}
@media (min-width: 1300px) {
  .page-header {
    margin-bottom: 82px;
    background: url("../img/index-background-desktop@1x.jpg"), linear-gradient(to right, #ffffff 50%, #64a83b 50%);
    background-position: right;
    background-repeat: no-repeat;
  }
  .page-header.page-header--inner {
    margin-bottom: 61px;
    background: transparent;
  }
  .page-header__container {
    width: 1220px;
    padding: 0 40px;
    padding-top: 54px;
  }
  .page-header__logo img {
    width: 202px;
    height: 59px;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
